:root {
  --black-color: #000;
  --white-color: #fff;
  --bg-color: #ae1332;
}

*, :before, :after {
  box-sizing: border-box;
}

body {
  background: var(--bg-color);
  color: var(--white-color);
  font-family: DM Sans, sans-serif;
  font-size: 14px;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

a {
  text-decoration: none;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  color: currentColor;
  font-family: inherit;
  line-height: inherit;
  border: none;
}

input, textarea {
  font-family: inherit;
  line-height: inherit;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

